// BASE------------------------------------------
$color1 : #f7f7f7;
$color2 : #212429;
$color3 : #f4f4f4;
*{
	transition:0.3s ease all;
}
body{
	font-family: 'Hind-Light';
	background: #f9f9fb
}
h1{
	font-family: 'Hind-Bold';
	text-shadow: 1px 2px 0 rgba(0, 0, 0, 0.44);
	color: #ffffff;
	font-size: 40px;
	line-height: 50px;
	text-transform: uppercase;
	letter-spacing: 5.6px;
}
@media screen and (max-width: 615px) {
	h1{
		line-height: 35px;
		font-size: 20px;	
		letter-spacing: 3px;
	}
}
h2{
	font-family: 'Montserrat';
	font-size: 20px;
	color: #212121;
	font-size: 20px;
	font-weight: 700;
	line-height: 28px;
	text-transform: uppercase;
	/* Text style for "Signup to" */
	letter-spacing: 4px
}
h3{
	font-family: 'Montserrat';
	font-size: 16px;
	color: #f9f9fb;
	line-height: 28px;
	text-transform: uppercase;
	/* Text style for "A, bout Bl" */
	letter-spacing: 0.96px;
	margin-bottom: 38px;
}
h4{
	color: #fff;
	font-family: 'Hind-Light';
	font-size: 14px;
	letter-spacing: 1.4px;
	margin-bottom: 19px;
	font-weight: 400;
}
.tac{
	text-align: center !important;
}
.container{
	width: 94%;
	margin: 0 auto;
	padding: 0
}
// HEADER------------------------------------------

#header{
	background: #212429;
	margin: 0;
	padding: 0;
		// FIRST ROW--
	// BURGER------------------------------------------
	.burger{
		width: 60px;
		text-align: center;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		cursor: pointer;
		border-left: 1px solid #3c3c3c;
		border-right: 1px solid #3c3c3c;
		height: 55px;
		transition:0.3s ease all;
		&:hover{background: white}
		span{
			width: 20px;
			height: 2px;
			background-color: #ffffff;
			display: block;
			margin: 1px 0px;
			border-top: 1px solid #212429;
			border-bottom: 1px solid #212429;
		}
	}
	// END BURGER------------------------------------------
	
	// LOGO------------------------------------------
	.logo{
		height: 55px;
		display: flex;
		align-items: center;
		text-decoration: none;
		.logo-part-1{
			height: 16px;
		}
		.logo-part-2{
			color: #fefefe;
			font-family: "Hind-Bold";
			font-size: 22px;
			font-weight: 400;
			display: block;
			text-transform: uppercase;
			margin-left: 9px;
			padding: 0;
		}
	}
	// END LOGO------------------------------------------
	
	// SOCIAL NAV------------------------------------------
	.social-nav{
		ul{
			width: 270px;
			margin: 0;
			margin-left: auto;
			padding: 0;
			height: 55px;
			display: flex;
			align-items: center;
			list-style: none;
			li{
				width: 55px;
				height: 55px;
				border-left: 1px solid #3c3c3c;
				border-right: 1px solid #3c3c3c;
				text-align: center;
				margin: 0;
				display: inline-flex;
				align-items: center;
				justify-content: center;
				transition:0.3s ease all;
				cursor:pointer;
				&:hover{background: white}
				a{
					color: white;
				}
			}
		}
	}
	@media screen and (max-width: 1024px) {
		.social-nav{
			display: none;
		}
	}
	//END SOCIAL NAV------------------------------------------
		// END FIRST ROW--

		// SECOND ROW--
	// BURGER MENU---------------------------------------------
	
	.burger-menu{
		overflow: hidden;
		ul{
			display: flex;
			flex-direction: column;
			text-align: center;
			width: 100%;
			padding: 0;
			margin: 0;
			height: 290px;
			li{
				margin: 0;
				padding: 0;
				height: calc(290px/5);
				transition:0.3s ease all;
				&:hover{
					background: white
				}
				a{
					text-transform: uppercase;
					color: white;
					text-decoration: none;
					font-family: 'Hind-Bold';
					line-height: calc(290px/5);
				}
			}
			// SOCIAL MENU OF BURGER MENU
			ul{
				flex-direction: row;
				height: calc(290px/5);
				display: none;
				li{
					border: none
				}
				&:hover{
					background: #212429
				}
			}
			// SOCIAL MENU OF BURGER MENU
		}
	}
	@media screen and (max-width: 1024px) {
		.burger-menu{
		overflow: hidden;
		ul{
			// SOCIAL MENU OF BURGER MENU
			.social-nav{
				display: block;
				ul{
					display: block;
				}
			}
			// SOCIAL MENU OF BURGER MENU
		}
	}
	  
	}
	// END BURGER MENU---------------------------------------------
		// END SECOND ROW--
}
.header-short{
	margin-bottom: 98px;
}
// END HEADER--------------------------------------

// MAIN--------------------------------------------
#main{
	width: 94%;
	margin: 0 auto;
	position: relative;
	background: #546174 url('../img/back1.png') no-repeat center center;
	background-size: cover;
	z-index: 2;
	transition:0.3s ease all;
	// JS---------------
	margin-top: -290px;
	margin-bottom: 60px;
	// JS END-----------
	
	// BACK START---------------------------------------------
	.back{
		height: 580px;
		position: relative;
		width: 100%;
		height: 580px;
		.text{
			position: absolute;
			width: 500px;
			left: 8%;
			top: 50%;
			transform:translate(0, -50%);
			span{
				color: #ffffff;
				font-family: "Hind-Light";
				font-size: 18px;
				line-height: 24px;
			}
		}

		.buttons{
			width: 250px;
			position: absolute;
			transform:translate(0, -50%);
			right: 0px;
			top: 50%;
			transform:rotate(-90deg);
			.squares{
				width: 42px;
				display: flex;
				justify-content: space-between;
				margin: 0 auto;
				margin-bottom: 15px;
				span{
					display: block;
					width: 8px;
					height: 8px;
					border: 1px solid #ffffff;
					background: none;
					transition:0.3s ease all;
					&:hover{cursor:pointer;background: #fff}
				}
			}
			.arrows{
				width: 250px;
				display: flex;
				justify-content: space-between;
				align-items: center;
				color: #ffffff;
				span{
					font-family: "Hind-Light";
					font-size: 14px;
					line-height: 24px;
					text-transform: uppercase;
					letter-spacing: 2.8px
				}
				a{
					color: #ffffff;
					overflow: hidden;

				}
			}
		}
		@media screen and (max-width: 1024px) {
			.buttons{
				transform:rotate(0deg);
				transform:translateX(50%);
				top:90%;
				right: 50%;
			}
		}
	}
	// BACK END---------------------------------------------
	
	.container{
		width: 100%;
		margin: 0;
	}
	// FORM START--------------------------------------------
	.form{
		padding: 28px 0px;
		box-shadow: 0 0 0 rgba(232, 232, 232, 0.11);
		border: 1px solid rgba(221, 221, 221, 0.15);
		background-color: #f7f7f7;
		width: 100%;
		margin: 0;
		input{
			color: #999999;
			font-family: 'Hind-Light';
			font-size: 15px;
			line-height: 28px;
			text-transform: uppercase;
			/* Text style for "Your Name" */
			letter-spacing: 1.5px;
			padding:20px 30px;
			border: none;
			width: 75%;
		}
		button{
			color: #ffffff;
			font-family: 'Hind-Bold';
			font-size: 14px;
			line-height: 24px;
			text-transform: uppercase;
			/* Text style for "S, ubscrib" */
			letter-spacing: 2.8px;
			box-shadow: 0 0 0 rgba(232, 232, 232, 0.11);
			border: 1px solid rgba(221, 221, 221, 0.15);
			background-color: #212121;
			width: 100%;
			padding:20px 30px;
		}
	}
	// END FORM START--------------------------------------------
}
// MAIN END----------------------------------------

