							line-height: 28px;
							letter-spacing: 2.1px;
						}
					}
				}
			}
		}
	}
	@media screen and (max-width: 1024px) {
		.aside-item{
			.item-list-height{
				overflow: hidden;
				transition:1s ease all;
				height: 0;
			}
		}
	}
}
