  color: #ffffff;
  font-family: 'Hind-Light';
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
  text-transform: uppercase;
  /* Text style for "@2015 copy" */
  letter-spacing: 1.4px;
  padding: 15px 0px; }

#aside {
  background-color: #ffffff;
  box-shadow: 0 0 0 rgba(232, 232, 232, 0.11);
  padding: 40px 30px;
  margin-bottom: 60px; }
  #aside .search {
    background-color: #f9f9fb;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }
    #aside .search input {
      width: 75%;
      color: #212121;
      font-family: 'Hind-Light';
      font-size: 15px;
      line-height: 28px;
      text-transform: uppercase;
      /* Text style for "S, earch" */
      letter-spacing: 1.5px;
      border: none;
      background-color: #f9f9fb; }
    #aside .search button {
      width: 25%;
      margin: 0;
      height: 50px;
      background-color: #212121;
      color: #ffffff;
      font-family: "Font Awesome";
      font-size: 16px;
      font-weight: 400;
      line-height: 24px;
      /* Text style for "" */
      letter-spacing: 0.19px;
      border: none; }
  #aside .aside-item {
    text-align: center;
    margin-top: 40px; }
    #aside .aside-item h2:hover {
      background: #212121;
      color: white;
      cursor: pointer; }
    #aside .aside-item h2:hover ~ .item-list,
    #aside .aside-item .item-list:hover {
      transition: 1s ease all; }
    #aside .aside-item .item-list {
      transform: scaleY(1);
      color: #212121;
      font-family: 'Hind-Medium';
      font-size: 16px;
      font-weight: 500;
      line-height: 60px;
      text-transform: uppercase;
      /* Text style for "lifestyle" */
      letter-spacing: 1.6px;
      transition: 1s ease all; }
      #aside .aside-item .item-list ul {
        margin: 0;
        padding: 0;
        margin-top: 19px; }
      #aside .aside-item .item-list li {
        list-style: none;
        border-top: 1px solid #d7d7d7; }
        #aside .aside-item .item-list li a {
          color: #212121;
          text-decoration: none;
          display: flex;
          justify-content: space-between; }
          #aside .aside-item .item-list li a .img {
            background-color: #f3f3f3;
            width: 100vw;
            height: 50%;
            margin-right: 10px;
            margin-top: 30px;
            margin-bottom: 30px; }
            #aside .aside-item .item-list li a .img img {
              width: 100%; }
          #aside .aside-item .item-list li a .text {
            min-width: 75%;
            margin-top: 30px; }
            #aside .aside-item .item-list li a .text h3 {
              color: #212121;
              text-align: left;
              margin-bottom: 0;
              line-height: 18px;
              margin-bottom: -20px; }
            #aside .aside-item .item-list li a .text .date, #aside .aside-item .item-list li a .text .likes {
              color: #999999;
              font-family: 'Hind-Light';
              font-size: 14px;
              font-weight: 300;
              line-height: 24px;
              text-transform: uppercase;
              /* Text style for "J, an. 16" */
              letter-spacing: 0.84px; }
        #aside .aside-item .item-list li .social-menu {
          margin: 0;
          padding: 0;
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-top: 40px; }
          #aside .aside-item .item-list li .social-menu li {
            border-top: 0px solid white; }
            #aside .aside-item .item-list li .social-menu li a {
              color: #464646;
              font-size: 21px;
              font-weight: 400;
              line-height: 28px;
              letter-spacing: 2.1px; }
  @media screen and (max-width: 1024px) {
    #aside .aside-item .item-list-height {
      overflow: hidden;
      transition: 1s ease all;
      height: 0; } }

#error404 {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center; }
  #error404 h1 {
    color: #363636;
    font-family: 'Montserrat';
    text-align: center;
    font-size: 300px;
    font-weight: 700;
    line-height: 24px;
    /* Text style for "404" */
    letter-spacing: 4.16px;
    position: relative;
    width: 50%;
    margin: 0 auto; }
    #error404 h1:after {
      content: 'Uh-oh! nothing found';
      position: absolute;
      width: 100%;
      display: block;
      height: 29px;
      background-color: #ffffff;
      transform: translate(center, -50%);
      color: #212121;
      font-size: 20px;
      line-height: 24px;
      text-transform: uppercase;
      letter-spacing: 14.24px;
      text-align: center;
      margin: 0 auto; }

@media screen and (max-width: 615px) {
  #error404 h1 {
    font-size: 100px; } }
